import React from 'react';
import { WhiteSpace, InputItem } from 'antd-mobile';
import { useHistory } from 'react-router-dom';
import {
  Page,
  PageTitle,
  PrimaryButton,
  OthersBox,
  OthersItem,
} from './styles';

const Login: React.FC = () => {
  const history = useHistory();
  return (
    <Page>
      <PageTitle>欢迎来到菓子题库</PageTitle>
      <InputItem placeholder="请输入邮箱/用户名" />
      <WhiteSpace size="lg" />
      <InputItem placeholder="请输入密码" type="password" />
      <PrimaryButton
        type="primary"
        mt="286px"
        onClick={(): void => history.push('/complete')}
      >
        登录
      </PrimaryButton>
      <OthersBox>
        <OthersItem onClick={(): void => history.push('/register')}>
          新用户注册
        </OthersItem>
        <OthersItem>忘记密码</OthersItem>
      </OthersBox>
    </Page>
  );
};

export default Login;
